	module.exports = {"data":{"markdownRemark":{"htmlAst":{"type":"root","children":[{"type":"element","tagName":"p","properties":{},"children":[{"type":"element","tagName":"page-intro","properties":{},"children":[{"type":"text","value":"The Carbon team currently only officially supports vanilla js and React. However developers using a different javascript framework (such as Angular or Vue) can can follow the instructions for the "},{"type":"element","tagName":"a","properties":{"href":"/getting-started/developers/vanilla"},"children":[{"type":"text","value":"Vanilla"}]},{"type":"text","value":" library to access the styles and build out their own components."}]}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h2","properties":{},"children":[{"type":"text","value":"Development"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"element","tagName":"strong","properties":{},"children":[{"type":"text","value":"Wrapping a component with a JavaScript framework of your choice"}]}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Many JavaScript frameworks have a mechanism to dynamically create/destroy DOM elements, for example, upon change in array.\nThis often makes it unclear when the DOM element to instantiate a Carbon component is available, which often depends on the JavaScript framework you use."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Also when DOM elements that Carbon components have been instantiated on are being destroyed, the Carbon component instances should be released so that e.g. there are no zombie event handlers."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"The easiest way to hook on the creation/destruction of DOM elements is by defining a \"wrapping component\", with the JavaScript framework of your choice. Here's an example using Web Components' "},{"type":"element","tagName":"a","properties":{"href":"https://developer.mozilla.org/en-US/docs/Web/Web_Components/Custom_Elements"},"children":[{"type":"text","value":"Custom Elements v1 spec"}]},{"type":"text","value":", but the notion of components, along with the lifecycle callbacks, are commonly found in many other JavaScript frameworks."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"pre","properties":{},"children":[{"type":"element","tagName":"code","properties":{"className":["language-javascript"]},"children":[{"type":"text","value":"class BXLoading extends HTMLElement {\n  // Called when this custom element gets into render tree\n  connectedCallback() {\n    // \"this\" here is \"<bx-loading>\" element\n    this.innerHTML = '(e.g. snippet from http://carbondesignsystem.com/components/loading/code)';\n    this.loading = CarbonComponents.Loading.create(this.querySelector('[data-loading]'));\n  }\n  // Called when this custom element gets out of render tree\n  disconnectedCallback() {\n    this.loading.release();\n  }\n}\ncustomElements.define('bx-loading', BXLoading);\n"}]}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h2","properties":{},"children":[{"type":"text","value":"Examples"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"element","tagName":"a","properties":{"href":"https://codepen.io/asudoh/pen/VryJBO?editors=1010"},"children":[{"type":"text","value":"Angular 4 wrapper for Carbon"}]}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h2","properties":{},"children":[{"type":"text","value":"Troubleshooting"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"If you experience any issues while getting set up with Carbon Components, please head over to the "},{"type":"element","tagName":"a","properties":{"href":"https://github.com/ibm/carbon-components"},"children":[{"type":"text","value":"Carbon Components GitHub repo"}]},{"type":"text","value":" for more guidelines and support. Please "},{"type":"element","tagName":"a","properties":{"href":"https://github.com/ibm/carbon-components/issues"},"children":[{"type":"text","value":"create an issue"}]},{"type":"text","value":" if your issue does not already exist."}]}],"data":{"quirksMode":false}},"fields":{"slug":"/getting-started/developers/other","currentPage":"other"},"frontmatter":{"title":"Developers","label":"Getting Started","tabs":["Vanilla","React","Other"]}}},"pathContext":{"slug":"/getting-started/developers/other","currentPage":"other"}}

